import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import UserMemberContainerWrapper from "../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../Ui/Border/BorderBottom";
import FormToggle from "../../../StaffTab/components/DividerTab/MainContent/StaffForm/FormToggle";
import style from "../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import NotifyEvent from "../../Utils/Snackbar/NotifyEvent";
import {
  useLmsDashboardMaster,
  useLmsTabController,
} from "../../Hooks/ApiHooks/LmsManager/lms-manager-api";
const InstituteLmsTabController = ({ lmid }) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    all_leave: true,
    all_moderator: true,
    data_export: true,
    all_staff: true,
    attendance: true,
    // biometric: true,
    biometric_linking: true,
    holiday: true,
    issued_leave: true,
    leave_allottment: true,
    leave_set_off_rule: true,
    manage_coff: true,
    request_leave: true,
    // request_tab: true,
  });
  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });
  const [lmsTabController] = useLmsTabController();
  const { lmsDashboardMaster } = useLmsDashboardMaster({
    data: {
      lmid: lmid,
      mod_id: "",
    },
    skip: !lmid,
  });

  useEffect(() => {
    if (lmsDashboardMaster?.tab_manage) {
      setState({
        all_leave: lmsDashboardMaster?.tab_manage?.all_leave,
        all_moderator: lmsDashboardMaster?.tab_manage?.all_moderator,
        data_export: lmsDashboardMaster?.tab_manage?.data_export,
        all_staff: lmsDashboardMaster?.tab_manage?.all_staff,
        attendance: lmsDashboardMaster?.tab_manage?.attendance,
        // biometric: lmsDashboardMaster?.tab_manage?.biometric,
        biometric_linking: lmsDashboardMaster?.tab_manage?.biometric_linking,
        holiday: lmsDashboardMaster?.tab_manage?.holiday,
        issued_leave: lmsDashboardMaster?.tab_manage?.issued_leave,
        leave_allottment: lmsDashboardMaster?.tab_manage?.leave_allottment,
        leave_set_off_rule: lmsDashboardMaster?.tab_manage?.leave_set_off_rule,
        manage_coff: lmsDashboardMaster?.tab_manage?.manage_coff,
        request_leave: lmsDashboardMaster?.tab_manage?.request_leave,
      });
    }
  }, [lmsDashboardMaster?.tab_manage]);

  const onSubmitFormHandler = (field, value) => {
    if (lmid) {
      const updatedObj = { ...state };
      for (let obj in updatedObj) {
        if (obj === field) {
          updatedObj[obj] = value;
        }
      }
      lmsTabController({
        lmid: lmid,
        tabManage: { tab_manage: updatedObj },
      })
        .then(() => {
          setSnackbar({
            msg: "Updated",
            open: true,
            variant: "success",
          });
        })
        .catch({});
      setState((prev) => ({
        ...prev,
        [field]: value,
      }));
    }
  };

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("tab_controller")}</h6>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <div className={style.fm_search}>
            <FormToggle
              labelHeading={t("all_staff")}
              checkedToggle={state.all_staff}
              onToggleHandler={(e) =>
                onSubmitFormHandler("all_staff", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <FormToggle
              labelHeading={t("attendance")}
              checkedToggle={state.attendance}
              onToggleHandler={(e) =>
                onSubmitFormHandler("attendance", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />

            <FormToggle
              labelHeading={t("leave_request")}
              checkedToggle={state.request_leave}
              onToggleHandler={(e) =>
                onSubmitFormHandler("request_leave", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
          </div>
          <BorderBottom />
          <div className={style.fm_search}>
            <FormToggle
              labelHeading={t("leave_issued")}
              checkedToggle={state.issued_leave}
              onToggleHandler={(e) =>
                onSubmitFormHandler("issued_leave", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <FormToggle
              labelHeading={t("manage_c_off")}
              checkedToggle={state.manage_coff}
              onToggleHandler={(e) =>
                onSubmitFormHandler("manage_coff", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />

            <FormToggle
              labelHeading={t("leave_allottment")}
              checkedToggle={state.leave_allottment}
              onToggleHandler={(e) =>
                onSubmitFormHandler("leave_allottment", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
          </div>
          <BorderBottom />
          <div className={style.fm_search}>
            <FormToggle
              labelHeading={t("leave_setoff_rule")}
              checkedToggle={state.leave_set_off_rule}
              onToggleHandler={(e) =>
                onSubmitFormHandler("leave_set_off_rule", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <FormToggle
              labelHeading={t("holiday")}
              checkedToggle={state.holiday}
              onToggleHandler={(e) =>
                onSubmitFormHandler("holiday", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <FormToggle
              labelHeading={t("all_leave")}
              checkedToggle={state.all_leave}
              onToggleHandler={(e) =>
                onSubmitFormHandler("all_leave", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
          </div>
          <BorderBottom />
          <div className={style.fm_search}>
            <FormToggle
              labelHeading={t("lms_moderators")}
              checkedToggle={state.all_moderator}
              onToggleHandler={(e) =>
                onSubmitFormHandler("all_moderator", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <FormToggle
              labelHeading={t("data_export")}
              checkedToggle={state.data_export}
              onToggleHandler={(e) =>
                onSubmitFormHandler("data_export", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <FormToggle
              labelHeading={t("biometric_linking")}
              checkedToggle={state.biometric_linking}
              onToggleHandler={(e) =>
                onSubmitFormHandler("biometric_linking", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
          </div>
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      <NotifyEvent eventState={snackbar} eventStateHandler={setSnackbar} />
    </>
  );
};

export default InstituteLmsTabController;
